import React from "react";
import TextField from "@material-ui/core/TextField";
import EditIcon from '@material-ui/icons/Edit';
import DeleteIcon from '@material-ui/icons/Delete';




export const renderEditableCell = (value, tableMeta, updateValue, rows) => {
  return (
    <div>
      <TextField
        style={{ width: "32%" }}
        id="standard-basic"
        defaultValue={value}
        onChange={(e) => {
          updateValue(e.target.value);
          tableMeta.rowData[1] = e.target.value;
          rows[tableMeta.rowIndex].count = e.target.value;
        }}
      />
    </div>
  );
};
export const columns = (rows) => [
  {
    name: "ano",
    label: "Curriculo",
    options: {
      filter: true,
      sort: true
    }
  },
  {
    name: "curso",
    label: "Curso",
    options: {
      filter: false,
      sort: true,
   
    }
  },
  {
    name: "botaoacao",
    label: "Opções",
    options: {
      filter: false,
      sort: false,
      customBodyRenderLite: (dataIndex, rowIndex) => (
        <div>
          <EditIcon
            onClick={() => alert("clicaste em editar")}
          />
          <DeleteIcon
            onClick={() => alert("clicaste em apagar")}
          />
        </div>
        
      )
    }
  }
];


