import React from 'react'
import { makeStyles } from '@material-ui/core/styles';
import TextField from '@material-ui/core/TextField';
import { Field, } from 'formik'


const useStyles = makeStyles({
  inputLabel: {
    fontSize: '1.2rem', // Ajuste o tamanho da fonte conforme necessário
  },
});

const Input = (props) => {
  const classes = useStyles();

  return (

   
      <Field as={TextField} name={props.name} id="outlined-full-width"
        label={props.label} size="small"  type={props.type}
        placeholder={props.placeholder} fullWidth margin="normal" InputLabelProps={{
          shrink: true,className: classes.inputLabel,
        }} variant="outlined" helperText={props.helperText} error={props.error}

      />
  );

}
export default Input